
.movies .movie{
  width: 45%; 
  background-color: white;
  margin-bottom: 70px;
  align-items: flex-start;
  display: flex;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  color: #adaeb9;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.25)
}

.movie img{
  position: relative; 
  top: -50px;
  max-width: 150px;
  width: 100%;
  margin-right: 30px;
}

.movie .movie__title, .movie .movie__year {
  margin: 0;
  font-weight: 300;
}

.movie .movie__title{
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  margin: 5px 0px;
  display: flex;
}

.movie__genres li{
  margin-right: 10px;
  font-size: 14px;
}